import React from 'react';
import Homep from '../../src/images/img2.svg';


const Home = () => {
    return (
        <>
            <div className="container">
                <div className="row mt-4">
                    <div className="col-md-5">
                        <main>
                            <h1>Get started with Bootstrap</h1>
                            <p className="fs-5">Quickly and easily get started with Bootstrap's compiled, production-ready files with this barebones example featuring some basic HTML and helpful links. Download all our examples to get started.</p>

                            <div className="mb-5">
                                <a href="/" className="btn btn-primary px-4">Download examples</a>
                            </div>
                        </main>

                    </div>

                    <div className="col-md-7">
                        <img src={Homep} alt="Home" className="img-fluid" />
                    </div>
                </div>
            </div>
        </>
    )
}

export default Home;